import React, { useEffect } from 'react';
const {kakao}=window;

const ResultMapRander = ({placeData}) => {
    const mapRandersPlaceData=[...placeData];

    useEffect(()=>{
        var mapContainer = document.getElementById('map'), //   div 
            mapOption = {
                center: new kakao.maps.LatLng(0,0), //  
                level: 3, //   
            };  
        //      
        var map = new kakao.maps.Map(mapContainer, mapOption);
        var linePath=[];
        var bounds=new kakao.maps.LatLngBounds();//      LatLngBounds  

        var marker,i;
        for(i=0; i<mapRandersPlaceData.length; i++){
            const {place_name, x, y}=mapRandersPlaceData[i];
            const WGS84=new kakao.maps.LatLng(y,x);
            const WTM= WGS84.toCoords();
            var temp;// 

            if(mapRandersPlaceData[i-1]===undefined){
                temp='';
            }else{
                temp=mapRandersPlaceData[i-1].place_name;
            };

            linePath.push(WGS84);

            marker = new kakao.maps.Marker({
                map: map, //   
                position: WGS84, //   
                title : place_name, //  ,      
                clickable:true
            });
            marker.setMap(map);
            bounds.extend(linePath[i]);// LatLngBounds   

            //    HTML  document element 
            var iwContent = `<div style="padding:8px;white-space:nowrap;">${i+1} : ${place_name}<br><a href="https://map.kakao.com/?map_type=TYPE_MAP&target=transit&rt=${',,'+WTM.La+','+WTM.Ma}&rt1=${temp}&rt2=${place_name}&rtIds=%2C&rtTypes=%2C" style="color:blue" target="_blank"></a></div>`,
                iwPosition = WGS84, //  
                iwRemoveable = true;

            var infowindow = new kakao.maps.InfoWindow({
                position : iwPosition, 
                content : iwContent,
                removable: iwRemoveable
            });
            
            kakao.maps.event.addListener(marker, 'click', makeOverListener(map, marker, infowindow));
        };

        var polyline = new kakao.maps.Polyline({
            path: linePath, //    
            strokeWeight: 5, //   
            strokeColor: '#f2020a', //  
            strokeOpacity: 0.7, //    1 0   0  
            strokeStyle: 'solid' //  
        });
        polyline.setMap(map);

        function setBounds(){
            // LatLngBounds       
            //       
            map.setBounds(bounds);
        };
        setBounds();

        //      
        function makeOverListener(map, marker, infowindow) {
            return function() {
                infowindow.open(map, marker);
            };
        };

    },[placeData]);

    return (
        <div id="map" style={{width:'100%', height:'500px'}}></div>
    );
};
export default ResultMapRander;