import React, { useEffect } from 'react';
const {kakao}=window;

const MapRander = ({placeData}) => {
    const mapRandersPlaceData=[...placeData];
    const checkedList=mapRandersPlaceData.filter(place=>place.checked===true);

    useEffect(()=>{
        var mapContainer = document.getElementById('map'), //   div 
            mapOption = {
                center: new kakao.maps.LatLng(35.95, 128.25), //  
                level: 13, //   
            };  
        //      
        var map = new kakao.maps.Map(mapContainer, mapOption);
        var points=[];
        var bounds=new kakao.maps.LatLngBounds();//      LatLngBounds  

        var marker,i;
        function setBounds(){
            // LatLngBounds       
            //       
            map.setBounds(bounds);
        };
        for(i=0; i<checkedList.length; i++){
            const {place_name, x, y}=checkedList[i];
            const coordinate=new kakao.maps.LatLng(y,x);

            points.push(coordinate);

            marker = new kakao.maps.Marker({
                map: map, //   
                position: new kakao.maps.LatLng(y, x), //   
                title : place_name, //  ,      
            })
            marker.setMap(map);

            bounds.extend(points[i]);// LatLngBounds   
            setBounds();
        };
    },[placeData]);

    return (
        <div>
            <div id="map" style={{width:'100%', height:'500px'}}></div>
        </div>
    );
};
export default MapRander;